<template>
    <Transition name="loginShow" appear>
        <div id="login-background" v-show="show">
            <div>
                <img src="../assets/head.png" style="border-radius: 50%; margin: 40px;box-shadow: 0 0 10px black;" alt="">
            </div>
            <div>
                <input class="login-input" type="text" v-model="username" placeholder="请输入账号">
                <br>
                <input class="login-input" type="password" v-model="password" placeholder="请输入密码">
                <br>
                <input class="login-submit" @click="login" type="submit" value="登录">
            </div>
        </div>
    </Transition>
</template>

<script>
export default {
    name: "loginin",
    data() {
        return {
            show: true,
            username: "",
            password: "",
        }
    },
    methods: {
        login() {
            if (this.username == "" && this.password == "") {
                // console.log("Get!!!");
                this.show = false;
                window.setTimeout(this.login1, 1000)
            }
        },
        login1() {
            this.$emit("getLoginFlag", true)
        }
    }
}
</script>

<style>
#login-background {
    background-color: rgba(240, 240, 240, 0.8);
    width: 400px;
    height: 400px;
    margin: auto;
    top: 50%;
    left: 50%;
    margin-top: -200px;
    margin-left: -200px;
    position: absolute;
    /* background-size: cover; */
    z-index: 0;
    border-radius: 20%;
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.8);
}


.login-input {
    /* border-radius: 0%; */
    margin: auto;
    margin-top: 20px;
    border-radius: 10px;
    padding: 5px;
}

.login-submit {
    /* border-radius: 0%; */
    margin: auto;
    margin-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 15px;
    background: linear-gradient(315deg, #42d392a8 25%, #647effa8);

}

.login-input:hover {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);

}

.login-submit:hover {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
    font-weight: bolder;
    background: linear-gradient(315deg, #42d392 25%, #647eff);


}

.loginShow-enter-active {
    animation: loginShow 1s;
}

.loginShow-leave-active {
    animation: loginShow 1s reverse;
}

@keyframes loginShow {
    from {
        transform: translateY(-200%);
    }

    to {
        transform: translateY(0px);
    }
}
</style>